
import XHeader from 'vux/src/components/x-header/index.vue'
import Actionsheet from 'vux/src/components/actionsheet/index.vue'
import TransferDom from 'vux/src/directives/transfer-dom/index.js'
import ButtonTab from 'vux/src/components/button-tab/button-tab.vue'
import ButtonTabItem from 'vux/src/components/button-tab/button-tab-item.vue'
import FormPreview from 'vux/src/components/form-preview/index.vue'
import XTable from 'vux/src/components/x-table/index.vue'
import XTextarea from 'vux/src/components/x-textarea/index.vue'
import XButton from 'vux/src/components/x-button/index.vue'
import XDialog from 'vux/src/components/x-dialog/index.vue'
import Group from 'vux/src/components/group/index.vue'
import Cell from 'vux/src/components/cell/index.vue'


export default {
name: 'ShowBill',
directives: {
TransferDom
},
components: {
XHeader,
Actionsheet,
ButtonTab,
ButtonTabItem,
FormPreview,
XTable,
XTextarea,
XButton,
XDialog,
Group,
Cell
},
data() {
return {
workflowCode: this.$store.getters.bill.bill_code,
list: [{
'label': '个人报销单代码',
'value': this.$store.getters.bill.bill_code
},
{
'label': '个人报销单名称',
'value': this.$store.getters.bill.bill_name
},
{
'label': '报销单性质',
'value': this.$store.getters.bill.bill_type
},
{
'label': '审批状态',
'value': this.$store.getters.bill.approve_state
},
{
'label': '申请日期',
'value': this.$store.getters.bill.apply_date
},
{
'label': '录入人',
'value': this.$store.getters.bill.checkin_man
},
{
'label': '审批日期',
'value': this.$store.getters.bill.approve_date
},
{
'label': '申请金额',
'value': this.$store.getters.bill.request_fee
},
{
'label': '审批金额',
'value': this.$store.getters.bill.bill_approve
},
{
'label': '审批人',
'value': this.$store.getters.bill.approve_man
},
{
'label': '部门',
'value': this.$store.getters.bill.bill_dept
},
{
'label': '借款人',
'value': this.$store.getters.bill.request_man
}
],
items: this.$store.getters.billDet,
buttonGroup: [{
style: 'primary',
text: '审批通过'
}, {
style: 'default',
text: '驳回'
}, {
style: 'default',
text: '转签',
link: '/'
}],
menus: [{
label: '审批通过',
type: 'primary',
value: 'approve',
otherProp: 'hey'
}, {
label: '驳回',
type: 'warn',
value: 'reject',
otherProp: 'hey'
}, {
label: '转签',
type: '',
value: 'transfer',
otherProp: 'hey'
}],
showMenus: false,
showApproveDialog: false,
showRejectDialog: false,
showTransferDialog: false
}
},
created: function () {
let data = {}
let successCallback = () => {}
let errorCallback = () => {}
this.$store.dispatch('showBill', {
data: data,
successCallback: successCallback,
errorCallback: errorCallback
})
this.$store.dispatch('showBillDet', {
data: data,
successCallback: successCallback,
errorCallback: errorCallback
})
},
mounted: function () {

},
methods: {
showDialog(key) {
if (key === 'approve') {
this.showApproveDialog = true
} else if (key === 'reject') {
this.showRejectDialog = true
} else if (key === 'transfer') {
this.showTransferDialog = true
}
},
approve() {
let data = {}
let successCallback = () => {
this.showApproveDialog = false
}
let errorCallback = () => {
this.showApproveDialog = false
}
this.$store.dispatch('approve', {
data: data,
successCallback: successCallback,
errorCallback: errorCallback
})
},
reject() {
let data = {}
let successCallback = () => {}
let errorCallback = () => {}
this.$store.dispatch('reject', {
data: data,
successCallback: successCallback,
errorCallback: errorCallback
})
},
transfer() {
let data = {}
let successCallback = () => {}
let errorCallback = () => {}
this.$store.dispatch('trandfer', {
data: data,
successCallback: successCallback,
errorCallback: errorCallback
})
}
}
}



